.parallax {
  padding: 50px 0 55px 0;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
  @include breakpoint(sm) {
    padding: 80px 0 45px 0; }
  @include breakpoint(lg) {
    min-height: 100vh; }

  &__container {}


  &__scene {}

  &__layer {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; }
  &__bg {
    background: linear-gradient(180deg, #4C79C3 0%, #7FA5E0 46.52%, #A3BEF4 100%); }
  &__layer-1 {
    background: url("../img/main-parallax/layer-3.png") center top no-repeat;
    background-size: cover; }
  &__layer-2 {
    background: url("../img/main-parallax/layer-2.png") center top no-repeat;
    background-size: cover; }
  &__layer-3 {
    background: url("../img/main-parallax/layer-1.png") center top no-repeat;
    background-size: cover; }
  &__layer-4 {
    background: url("../img/main-parallax/layer-4.png") center top no-repeat;
    background-size: cover; }

  &__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 500px;
    padding: 40px 0;
    position: relative;
    margin: 0 0 55px 0;
    @include breakpoint(lg) {
      //margin: 40px 0 55px 0
      margin: 0; }
    &:before {
      content: '';
      position: absolute;
      width: 300%;
      left: -100%;
      top: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.1);
      @include breakpoint(lg) {
        width: 130%;
        left: -15%;
        transform: skew(-.312rad); } } }


  &__desc {
    font-size: 18px;
    line-height: 30px;
    color: #ffffff;
    display: block;
    margin: 0 0 20px 0;
    z-index: 1; }


  &__btn {
    display: flex;
    justify-content: flex-start;
    z-index: 1; }


  &__body {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    z-index: 1;
    justify-content: center; }



  &__title {
    font-size: 35px;
    line-height: 40px;
    padding: 0 0 0 25px;
    margin: 0 0 20px 0;
    &:before {
      width: 7px;
      background-color: #D11586; }
    @include breakpoint(sm) {
      font-size: 38px;
      line-height: 45px; } } }
